.connect-wrap { padding: 40px 0 0; width: 100%; min-height: 500px; }

.connect-wrap .connect-heading { text-align: center; min-height: 200px; background: #fff; padding: 10px 0; text-align: center; }

.connect-wrap .connect-heading h2 { font-size: 40px; color: #3b3b3a; font-weight: 500; text-transform: uppercase; margin-bottom: 13px; margin-top: 5px; }

.connect-wrap .connect-heading section p, .connect-wrap .connect-heading section { font-size: 15px; color: #3b3b3a; }

.connect-wrap-form { background: #3b3b3a; padding: 40px 0; min-height: 400px; }

.connect-wrap-form input[type="text"], .connect-wrap-form input[type="email"], .connect-wrap-form input[type="tel"] { width: 100%; min-height: 45px; border: 1px solid #ccc; margin: 0 0 15px; border-radius: 0px; outline: none; padding: 5px 15px; font-family: "Ubuntu"; }

.connect-wrap-form input[type="text"]:focus, .connect-wrap-form input[type="email"]:focus, .connect-wrap-form input[type="tel"]:focus { outline: none !important; }

.connect-wrap-form input[type="text"]::placeholder, .connect-wrap-form input[type="text"]::-moz-placeholder, .connect-wrap-form input[type="email"]::placeholder, .connect-wrap-form input[type="email"]::-moz-placeholder, .connect-wrap-form input[type="tel"]::placeholder, .connect-wrap-form input[type="tel"]::-moz-placeholder { color: #a4a1a1; }

.connect-wrap-form textarea { width: 100%; height: 120px; border: 1px solid #ccc; margin: 0 0 15px; border-radius: 0px; outline: none; padding: 5px 15px; }

.connect-wrap-form textarea::placeholder, .connect-wrap-form textarea::-moz-placeholder { color: #a4a1a1; text-transform: capitalize; }

.connect-wrap-form input[type="button"] { background: url("../minify/images/btn-bckgrnd.jpg") left repeat-x; border: none; float: right; width: 140px; height: 40px; color: #fff; font-weight: 300; font-size: 18px; font-family: "Ubuntu"; }

/**Footer**/
.main-footer { min-height: 240px; position: relative; width: 100%; padding-top: 0px; }

.main-footer .footer-top { min-height: 200px; background: url(../minify/images/footer-bckgrnd.jpg) no-repeat; background-size: cover; background-position: top center; color: #d9d9d9; padding-top: 40px; }

.main-footer .footer-top .footer-box { min-height: 200px; padding-bottom: 10px; }

.main-footer .footer-top .footer-box figure { margin: 0 0 10px; }

.main-footer .footer-top .footer-box section { color: #d9d9d9; font-size: 14px; }

.main-footer .footer-top .footer-box section p { color: inherit; font-size: 16px; }

.main-footer .footer-top .footer-box h4 { color: #d9d9d9; font-size: 19px; margin: 0 0 15px; }

.main-footer .footer-top .address { position: relative; padding-left: 24px; font-size: 16px; }

.main-footer .footer-top .address:before { content: "\f041"; font: normal normal normal 22px/1 FontAwesome; color: #d9d9d9; position: absolute; left: 0px; }

.main-footer .footer-top .socialfooter ul { margin: 0px; padding: 0px; list-style: none; }

.main-footer .footer-top .socialfooter ul li { display: inline-block; margin: 0 15px 0 0; color: #d9d9d9; }

.main-footer .footer-top .socialfooter ul li a { color: #d9d9d9; font-size: 16px; border: 1px solid #d9d9d9; border-radius: 50px; width: 35px; height: 35px; text-align: center; display: block; padding: 4px; }

.main-footer .footer-top .box2 { text-align: center; justify-items: center; align-content: center; padding: 20px 0 0 0; }

.main-footer .footer-top .box2 ul { margin: 0px; padding: 0px; list-style: none; }

.main-footer .footer-top .box2 ul li { display: inline-block; margin: 0 10px 34px; color: #d9d9d9; list-style: none; width: 45%; position: relative; }

.main-footer .footer-top .box2 ul li:nth-child(2), .main-footer .footer-top .box2 ul li:nth-child(4) { margin-right: 0px; }

.main-footer .footer-top .box2 ul li:nth-child(1) a:before { content: "\f095"; font: normal normal normal 20px/1 FontAwesome; display: block; position: absolute; top: 3px; left: 10px; }

.main-footer .footer-top .box2 ul li:nth-child(2) a:before { content: "\f003"; font: normal normal normal 20px/1 FontAwesome; display: block; position: absolute; top: 3px; left: 10px; }

.main-footer .footer-top .box2 ul li:nth-child(3) a:before { content: "\f1ac"; font: normal normal normal 20px/1 FontAwesome; display: block; position: absolute; top: 3px; left: 10px; }

.main-footer .footer-top .box2 ul li:nth-child(4) a:before { content: "\f0ac"; font: normal normal normal 20px/1 FontAwesome; display: block; position: absolute; top: 3px; left: 10px; }

.main-footer .footer-top .box2 ul li a { color: #d9d9d9; font-size: 16px; position: relative; padding-left: 40px; display: inline-block; }

.main-footer .footer-btm { min-height: 50px; color: #4e4d4c; font-size: 15px; background: #000; padding: 12px 0px; }

.main-footer .footer-btm .footerlogo img { width: 75%; }

.main-footer .footer-btm section p { margin: 0px; color: #4e4d4c; font-size: 16px; padding: 3px 0px 0; }

/**Scroll***/
#back-top > a { bottom: 10%; color: #484751; display: block; font-size: 37px; border: 2px solid #484751; border-radius: 50px; position: fixed; right: 1%; text-align: center; width: 60px; height: 60px; }

#back-top > a img { width: 40px; display: block; margin: 5px auto; }

/**pagition**/
.pagition ul { margin: 0px auto; padding: 0px; list-style: none; width: 55%; }

.pagition ul li { display: inline-block; font-weight: 300; font-size: 14px; margin: 0 10px 0 0; text-align: center; vertical-align: middle; }

.pagition ul li span { width: 41px; height: 41px; display: block; background: #484751; border-radius: 25px; color: #fff; font-size: inherit; padding: 10px 14px; }

.pagition ul li a { width: 41px; height: 41px; background: #eeeeee; text-align: center; border-radius: 25px; display: block; color: #000; font-size: inherit; padding: 10px 14px; }

/**responsive**/
@media (min-width: 100px) and (max-width: 900px) { .heading-title-feat { text-align: center; }
  .featuredimg-container .breadcrumbs_wrap { float: none; text-align: center; margin: 0px auto; display: block; }
  .footer-top-box { margin: 0 0 15px; }
  .slider .inner-item .caption { top: 20%; }
  .slider .slider-text-con { width: 80%; padding: 30px 10px 20px 20px; background-image: none; }
  .serv-col, .portfolio-wrap-box { flex-basis: auto; }
  .client-logo, .client-logo2 { flex-basis: auto; }
  .portfolio-wrap-box section h4 { font-size: 18px; }
  .portfolio-wrap-box section { padding: 40px 30px; min-height: 200px; } }
